.product-card {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 400;
  width: $card-width;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  padding: 32px 16px 16px;
  margin-right: $card-margin-right;
  margin-bottom: $card-margin-bottom;
  &:nth-child(4n) {
    margin-right: 0;
  }
  &__image {
    width: 160px;
    height: 134px;
    align-self: center;
    margin-bottom: 40px;
  }

  &__name {
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: #060b35;
    margin-bottom: 4px;
  }
  &__code {
    font-size: 10px;
    color: #616070;
    margin-bottom: 17px;
  }
  &__review {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }
  &__review-text {
    font-size: 10px;
    line-height: 14px;
    color: #060b35;
  }
  &__price {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
    line-height: 18px;
  }
  &__price-text {
    font-size: 12px;
    color: #616070;
  }
  &__price-value {
    font-weight: 700;
    font-size: 16px;
  }
  &__button-buy {
    width: 166px;
    min-height: 40px;
    text-decoration: none;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    text-transform: uppercase;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #00acdc;
    border-radius: 5px;
    border: 1px solid #00acdc;
    &:hover {
      background-color: #fff;
      color: #00acdc;
    }
  }
}
